<template>
<div class="article">
  <h2 class="article_hd"> </h2>
  <div class="article_con">
  <p class="callName"></p>
  <p v-for="para in content_1st">{{para.value}}</p>
  <img src="" alt=""/>
  <h3></h3>
  <p v-for="para in content_2ed">{{para.value}}</p>
  <img src="" alt=""/>
  <p v-for="para in content_3th">{{para.value}}</p>
</div>
</div>
</template>

<script type="text/ecmascript-6">
    export default{
        data(){
            return {
              content_1st:[
                {id:'01',value:''},
                {id:'02',value:''},
              ],
              content_2ed:[
                {id:'01',value:''},
                {id:'02',value:''},
                {id:'03',value:''},
              ],
              content_3th:[
                {id:'01',value:''},
                {id:'02',value:''},
              ],
            }
        }
    }
</script>


<style scoped lang="scss">
.article{
  text-align: center;
background:#fff;
  .article_hd{
    text-align: center;
    height: 50px;
    line-height: 50px;
    font-size: 30px;
    font-weight: bold;
  }
  .article_con{
    padding: 5px;
    p{
      text-indent: 36px;
      font-size: 18px;
      text-align: left;
    }
  }
}
</style>



