<template>
  <div>
    <div class="FootBan">
      <img :src="foot_ban.src" :alt="foot_ban.value"/>
    </div>
    <div class="contact">
      <h5></h5>
      <div class="form">
        <div class="name"><span>:</span><input type="text" placeholder=""/></div>
        <div class="telphone"><span>:</span><input type="text" placeholder=""/></div>
        <input type="submit"  value="" class="submit"/>
      </div>
    </div>
    <div class="business_info">
      <p>10086</p>
      <p>qq10000</p>
      <p>10010</p>
      <p>2</p>
      <p>2014-2016 d31123231cp</p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
    export default{
        data(){
            return {
              foot_ban:{id:'01',value:'',src:require('@/assets/images/bananer/footban.png')}
            }
        }
    }
</script>


<style scoped>
  .FootBan{
    margin: 0 auto;
    margin-top: 20px;
    width: 90%;
    height: 295px;
  }
  .FootBan img{
    width: 100%;
    height: 100%;
  }
  .contact{
    text-align: center;
    border: 1px solid black;
    margin: 20px auto;
    width: 90%;
    height: 200px;
    border-radius: 10px;
    padding: 25px 0 0;
  }
  .contact h5{
    color: #0055aa;
    font-weight: bold;
  }
  .form div{
    margin: 10px auto;
    width: 100%;
    height: 32px;
  }
  .name input,.telphone input{
    width: 300px;
    height: 30px;
    margin-left: 5px;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid slategrey;
  }
  .submit{
    border-radius: 5px;
    border: none;
    width: 90%;
    height: 40px;
    line-height: 40px;
    background: #ff5a60;
    color: white;
  }
  .business_info{
    margin-bottom: 60px;
    text-align: center;
  }
  .business_info p{
    margin-top: 5px;
    height: 20px;
    line-height: 20px;
  }
</style>



